<style scoped>
  .choose-store-wrap {
    max-height: 460px;
    overflow-y: auto;
  }
  .store-pre-time-item-wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
  }
  .store-pre-time-item-wrap>.item-left {
    flex: 0 0 90px;
    width: 90px;
    text-align: left;
    margin-left: 20px
  }
  .store-pre-time-item-wrap>.item-right {
    border: 1px solid #ececec;
    border-radius: 8px;
    padding: 8px;
    width: 500px;
    box-sizing: border-box;
  }
</style>
<template>
  <div>
    <el-dialog 
      :close-on-click-modal = "false"
      :close-on-press-escape = "false"
      :title="title" 
      :visible.sync="isShow" 
      width='700px'>
      <div class="choose-store-wrap">
        <div class="store-pre-time-item-wrap" :class="index > 0 ? 'mt10' : null" v-for="(item, index) of activityStoreGroupList" :key="index">
          <div class="item-right">
            <div>
              <span>参与门店</span>
              <el-button size="small" @click="chooseStoreOpen(index)" icon="el-icon-plus">选择门店</el-button>
              <div v-if="item.activityStoresCheckedList.length > 0">
                <el-table
                  class="fixed-height"
                  :data="item.activityStoresCheckedList"
                  empty-text="暂无选中任何门店"
                  max-height="250"
                  size="small">
                  <el-table-column min-width="200px" label="门店编码/名称">
                    <template slot-scope="scope">
                      <div>
                        {{scope.row.erpStoreNo}}_{{scope.row.erpStoreName}}
                      </div>
                    </template>
                  </el-table-column>
                  <el-table-column min-width="80" prop="cityName" label="城市"></el-table-column>
                  <el-table-column min-width="80" prop="saleTypeName" label="门店类型"></el-table-column>
                  <el-table-column
                    align="center"
                    label="操作"
                    width="100">
                    <template slot-scope="scope" size="small">
                      <el-button @click="deleteStoreItem(index, scope.row)" type="text" size="mini" icon="el-icon-delete">删除</el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </div>
            </div>
            <div class="mt10">
              <span>履约时间</span>
              <el-date-picker
                class="search-input"
                v-model="item.performanceDate"
                type="date"
                placeholder="请选择履约时间"
                format="yyyy-MM-dd"
                value-format="yyyy-MM-dd"
                size="small"
                :picker-options="pickerOptions">
              </el-date-picker>
              <span>门店仅支持自提则为自提时间</span>
            </div>
          </div>
          <div class="item-left">
            <el-button type="primary" size="mini" icon="el-icon-plus" circle @click="doAddStoreGroupHandel"></el-button>
            &nbsp;
            <el-button size="mini" icon="el-icon-minus" circle v-if="activityStoreGroupList.length > 1" @click="doMinusStoreGroupHandel(index)"></el-button>
          </div>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="confirmClose" size="mini" :disabled="isDisabled">确 定</el-button>
      </div>
    </el-dialog>
    <choose-store ref="chooseStore" @confirmChooseStore="confirmChooseStore"></choose-store>
  </div>
</template>
<script>
  import ChooseStore from "./ChooseStore"
  export default {
    data() {
      return {
        activityType: '',
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7; // 如果没有后面的-8.64e7就是不可以选择今天的
          }
        }, 
        isDisabled: false,
        isShow: false,
        title: '',
        // 参与门店与履约时间
        activityStoreGroupList: [
          {
            performanceDate: '', // 履约时间
            activityStoresCheckedList: [] // 选中的门店
          }
        ],
        activityEndDate: '',
        platformId: '',
        regionCode: ''
      }
    },
    components: {
      "choose-store": ChooseStore
    },
    methods: {
      /**
       * [setDefault 设置基本选中]
       * @param {[type]} _val [description]
       */
       setDefault(_val) {
        if(_val) {
          this.title = _val.title
          this.activityType = _val.activityType
          this.activityEndDate = _val.activityEndDate
          this.platformId = _val.platformId
          this.regionCode = _val.regionCode
        }

        this.isDisabled = false
        this.isShow = true
        this.activityStoreGroupList = [
          {
            performanceDate: '', // 履约时间
            activityStoresCheckedList: [] // 选中的门店
          }
        ]
      },
      /**
       * [chooseStoreOpen 选择门店]
       * @return {[type]} [description]
       */
       chooseStoreOpen(groupIndex) {
        let _params = {
          title: '选择门店',
          groupIndex: groupIndex,
          platformId: this.platformId,
          regionCode: this.regionCode,
          checkedList: this.activityStoreGroupList[groupIndex].activityStoresCheckedList,
          otherCheckedList: this.getCheckedStores(groupIndex)
        }
        this.$refs.chooseStore.setDefault(_params)
      },
      /**
       * [confirmChooseStore 选中的门店]
       * @param  {[type]} _value [description]
       * @return {[type]}        [description]
       */
       confirmChooseStore(_value) {
        let activityStoresCheckedList = _value.checkedList || []
        let groupIndex = _value.groupIndex || 0
        this.activityStoreGroupList[groupIndex].activityStoresCheckedList = activityStoresCheckedList
      },
      /**
       * @description: 计算出所有门店和履约时间分组中已经选中的门店
       * @param {type} 
       * @return: 
       */
       getCheckedStores(excludeIdx) {
        let allCheckedStores = []
        if(excludeIdx || excludeIdx === 0) {
          this.activityStoreGroupList.forEach((item, idx) => {
            if(idx != excludeIdx) {
              allCheckedStores = [...allCheckedStores, ...item.activityStoresCheckedList]
            }
          })
        }else {
          this.activityStoreGroupList.forEach(item => {
            allCheckedStores = [...allCheckedStores, ...item.activityStoresCheckedList]
          })
        }
        return allCheckedStores
      },
      doAddStoreGroupHandel() {
        let groupItem = {
          performanceDate: '',
          activityStoresCheckedList: []
        }
        this.activityStoreGroupList.push(groupItem)
      },
      doMinusStoreGroupHandel(index) {
        this.activityStoreGroupList.splice(index, 1)
      },
      /**
       * [deleteItem 删除选中的门店]
       * @param  {[type]} row [description]
       * @return {[type]}      [description]
       */
       deleteStoreItem(groupIndex, row) {
        let erpStoreNo = row.erpStoreNo
        let checkedStoreList = this.activityStoreGroupList[groupIndex].activityStoresCheckedList

        this.activityStoreGroupList[groupIndex].activityStoresCheckedList = checkedStoreList.filter(item => {
          return item.erpStoreNo !== erpStoreNo
        })
      },
      /**
       * @description: 批量引入门店提交
       * @param {type} 
       * @return: 
       */
      confirmClose() {
        let activityStoreGroupList = this.activityStoreGroupList.map((i, idx) => {
          return {
            index: idx,
            performanceDate: i.performanceDate,
            storeNoList: i.activityStoresCheckedList.map(k => {
              return k.erpStoreNo
            })
          }
        })
        let activityEndDateNum = Date.parse(new Date(this.activityEndDate.replace(/\-/g, '/')))
        for(let item of activityStoreGroupList) {
          if(!item.storeNoList || !item.storeNoList.length) {
            this.$message(`请选择【第${item.index + 1}组】的参与门店`)
            return
          }
          if(item.performanceDate) {
            let performanceDateNum = Date.parse(new Date(`${item.performanceDate} 23:59:59`.replace(/\-/g, '/')))
            if(activityEndDateNum > performanceDateNum) {
              this.$message(`【第${item.index + 1}组】的履约时间不能早于活动结束时间，所选活动商品的结束时间为${this.activityEndDate}`)
              return
            }
            item.performanceDatetime = `${item.performanceDate} 23:59:59`
          }else {
            this.$message(`请选择【第${item.index + 1}组】的履约时间`)
            return
          }
        }
        this.isDisabled = true
        let params = {
          reqParams: activityStoreGroupList,
          activityType: this.activityType
        }
        this.$emit("doBatchInsertHandel", params)
      },
      closeDialog() {
        this.isShow = false
      },
      closeDisabled() {
        this.isDisabled = false
      }
    }
  }
</script>


