<style>
  .p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .img-w {
    width: 80px;
    height: 80px;
  }
  .img-w>img {
    width: 80px;
  }
  .p-m-c-r {
    padding: 0 10px;
  }
  .erp-p-no {
    color: #666;
    font-size: 12px;
  }

  .date-wrap-inline {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
  }

  .stock-num .el-button--mini{
    padding: 6px 5px;
  }
  .act-name-wrap {
    margin: 4px 0;
    padding: 0 5px;
    border: 1px solid red;
    line-height: 20px;
    text-align: center;
    box-sizing: border-box;
  }
</style>

<template>
  <div>
    <!-- 操作框 -->
    <div class="search-form-wraper">
      <el-form :model="searchContent" label-position="top">
        <region class="region-wrap" @RegionChange="RegionChange" :refresh="false"></region>
        <el-form-item label="商品分类">
          <el-cascader
            placeholder="请选择分类"
            class="search-input"
            :options="categoryList"
            change-on-select
            size="small"
            clearable
            filterable
            @change="doSearchHandel(true)"
            :props="cateDefaultProps"
            v-model="searchContent.categoryIdList">
          </el-cascader>
        </el-form-item>
        <el-form-item label="SKU编码/商品名称">
          <el-input class="search-input" v-model.trim="searchContent.goodsInfo" placeholder="SKU编码/商品名称" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>
        <el-form-item label="区域商品ID">
          <el-input class="search-input" v-model.trim="searchContent.platformCityGoodsId" type="number" placeholder="区域商品ID" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>
        <el-form-item label="活动ID/名称">
          <el-input class="search-input" v-model.trim="searchContent.activitySearchVal" placeholder="活动ID/名称" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>
        <el-form-item label="活动类型">
          <el-select class="search-input" v-model="searchContent.activityType" filterable placeholder="请选择活动类型" size="small" @change="doSearchHandel(true)" clearable>
             <el-option
               v-for="item in activityTypeOptions"
               :key="item.value"
               :label="item.label"
               :value="item.value">
             </el-option>
           </el-select>
        </el-form-item>
        <el-form-item label="状态">
          <el-select class="search-input" v-model="searchContent.activityStatus" filterable placeholder="请选择状态" size="small" @change="doSearchHandel(true)" clearable>
             <el-option
               v-for="item in activityStatusOptions"
               :key="item.value"
               :label="item.label"
               :value="item.value">
             </el-option>
           </el-select>
        </el-form-item>
        <el-form-item label="是否顺丰仓现货">
          <el-select class="search-input" v-model="searchContent.sfInStock" filterable placeholder="请选择状态" size="small" @change="doSearchHandel(true)" clearable>
             <el-option
               v-for="item in sfInStockOptions"
               :key="item.value"
               :label="item.label"
               :value="item.value">
             </el-option>
           </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>
    
    <!-- 搜索结果 -->
    <div class="table-wraper">
      <div class="batch-handel-w">
        <el-button class="sup-mini" size="mini" icon="el-icon-edit" @click="importStore" :disabled="checkedList.length === 0" >发布至门店</el-button>
        <el-button class="sup-mini" size="mini" icon="el-icon-delete" :disabled="checkedList.length === 0" @click="deleteHandel(false, checkedList)">批量删除</el-button>
      </div>
      <el-table
        class="fixed-height"
        v-loading="loading"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        @select="handleSelect"
        @select-all="handleSelectAll"
        size="small">
        <el-table-column
          type="selection"
          align="center">
        </el-table-column>
        <el-table-column min-width="160px" label="ID">
          <template slot-scope="scope">
            <div class="ll-erp-p-no">活动ID: {{scope.row.activityId}}</div>
            <div class="ll-erp-p-no">活动商品ID: {{scope.row.activityPlatCityGoodsId}}</div>
            <div class="ll-erp-p-no">SKU编码：{{scope.row.goodsNo}}</div>
          </template>
        </el-table-column>
        <el-table-column min-width="250px" label="商品信息">
          <template slot-scope="scope">
            <div class="p-m-w">
              <div class="img-w g-m-r">
                <img v-if="scope.row.imgUrl && scope.row.imgUrl != ''" :src="scope.row.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                <img v-else src="/shop/static/img_error.png" alt="">
              </div>
              <div class="p-m-c-r">
                <div>{{scope.row.goodsName || '-'}}</div>
                <div class="ll-erp-p-no">商品编码：{{scope.row.skuNo || '--'}}</div>
                <div class="ll-erp-p-no">区域商品ID：{{scope.row.platformCityGoodsId || '--'}}</div>
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="100px" prop="spec" label="规格"></el-table-column>
        <el-table-column label="原价" min-width="160px">
          <template slot-scope="scope">
            <div v-if="showUpdate && currentFiledName === 'price' && currentIndex === scope.$index" class="stock-num">
              <el-button-group>
                <el-input v-model="scope.row.price" size="mini" style="width: 80px;" @blur="onBlurHandel"></el-input>
              </el-button-group>
              <el-button-group>
                <el-button icon="el-icon-check" size="mini" style="width: 25px;" @click="sureUpdateHandel(scope.row)"></el-button>
                <el-button icon="el-icon-close" size="mini" style="width: 25px;" @click="onBlurHandel"></el-button>
              </el-button-group>
            </div>
            <div v-else class="stock-num">
              <el-button-group>
                <el-input v-model="scope.row.price" size="mini" style="width: 80px;" @focus="onFocusHandel(scope.$index, 'price')" :disabled="scope.row.activityStatus === 4 || scope.row.activityStatus === 5"></el-input>
              </el-button-group>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="活动价" min-width="160px">
          <template slot-scope="scope">
            <div v-if="showUpdate && currentFiledName === 'preSalePrice' && currentIndex === scope.$index" class="stock-num">
              <el-button-group>
                <el-input v-model="scope.row.preSalePrice" size="mini" style="width: 80px;" @blur="onBlurHandel"></el-input>
              </el-button-group>
              <el-button-group>
                <el-button icon="el-icon-check" size="mini" style="width: 25px;" @click="sureUpdateHandel(scope.row)"></el-button>
                <el-button icon="el-icon-close" size="mini" style="width: 25px;" @click="onBlurHandel"></el-button>
              </el-button-group>
            </div>
            <div v-else class="stock-num">
              <el-button-group>
                <el-input v-model="scope.row.preSalePrice" size="mini" style="width: 80px;" @focus="onFocusHandel(scope.$index, 'preSalePrice')" :disabled="scope.row.activityStatus === 4 || scope.row.activityStatus === 5"></el-input>
              </el-button-group>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="区域库存" min-width="160px">
          <template slot-scope="scope">
            <div v-if="showUpdate && currentFiledName === 'platformCurStock' && currentIndex === scope.$index" class="stock-num">
              <el-button-group>
                <el-input v-model="scope.row.platformCurStock" size="mini" style="width: 80px;" @blur="onBlurHandel"></el-input>
              </el-button-group>
              <el-button-group>
                <el-button icon="el-icon-check" size="mini" style="width: 25px;" @click="sureUpdateHandel(scope.row)"></el-button>
                <el-button icon="el-icon-close" size="mini" style="width: 25px;" @click="onBlurHandel"></el-button>
              </el-button-group>
            </div>
            <div v-else class="stock-num">
              <el-button-group>
                <el-input v-model="scope.row.platformCurStock" size="mini" style="width: 80px;" @focus="onFocusHandel(scope.$index, 'platformCurStock')" :disabled="scope.row.activityStatus === 4 || scope.row.activityStatus === 5"></el-input>
              </el-button-group>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="每人限购" min-width="160px">
          <template slot-scope="scope">
            <div v-if="showUpdate && currentFiledName === 'limitCount' && currentIndex === scope.$index" class="stock-num">
              <el-button-group>
                <el-input v-model="scope.row.limitCount" size="mini" style="width: 80px;" @blur="onBlurHandel"></el-input>
              </el-button-group>
              <el-button-group>
                <el-button icon="el-icon-check" size="mini" style="width: 25px;" @click="sureUpdateHandel(scope.row)"></el-button>
                <el-button icon="el-icon-close" size="mini" style="width: 25px;" @click="onBlurHandel"></el-button>
              </el-button-group>
            </div>
            <div v-else class="stock-num">
              <el-button-group>
                <el-input v-model="scope.row.limitCount" size="mini" style="width: 80px;" @focus="onFocusHandel(scope.$index, 'limitCount')" :disabled="scope.row.activityStatus === 4 || scope.row.activityStatus === 5"></el-input>
              </el-button-group>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="排序" min-width="160px">
          <template slot-scope="scope">
            <div v-if="showUpdate && currentFiledName === 'sortNum' && currentIndex === scope.$index" class="stock-num">
              <el-button-group>
                <el-input v-model="scope.row.sortNum" size="mini" style="width: 80px;" @blur="onBlurHandel"></el-input>
              </el-button-group>
              <el-button-group>
                <el-button icon="el-icon-check" size="mini" style="width: 25px;" @click="sureUpdateHandel(scope.row)"></el-button>
                <el-button icon="el-icon-close" size="mini" style="width: 25px;" @click="onBlurHandel"></el-button>
              </el-button-group>
            </div>
            <div v-else class="stock-num">
              <el-button-group>
                <el-input v-model="scope.row.sortNum" size="mini" style="width: 80px;" @focus="onFocusHandel(scope.$index, 'sortNum')" :disabled="scope.row.activityStatus === 4 || scope.row.activityStatus === 5"></el-input>
              </el-button-group>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="90px" label="顺丰仓现货">
          <template slot-scope="scope">
            <span>{{scope.row.sfInStock ? '是' : '否'}}</span>
          </template>
        </el-table-column>
        <el-table-column min-width="90px" prop="activityTypeName" label="活动类型"></el-table-column>
        <el-table-column min-width="120px" prop="activityName" label="活动名称"></el-table-column>
        <el-table-column min-width="210px" label="活动时间">
          <template slot-scope="scope">
            <div class="date-wrap-inline" v-if="scope.row.activityStartDate && scope.row.activityEndDate">
              <div>
                <p>{{scope.row.activityStartDate.substr(0, 10)}}</p>
                <p>{{scope.row.activityStartDate.substr(11, 8)}}</p>
              </div>
              <div>&nbsp;-&nbsp;</div>
              <div>
                <p>{{scope.row.activityEndDate.substr(0, 10)}}</p>
                <p>{{scope.row.activityEndDate.substr(11, 8)}}</p>
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="120px" prop="activityStatus" label="活动状态" :formatter="activityStatusFormatter"></el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="80">
          <template slot-scope="scope" size="small">
            <el-button @click="deleteHandel(true, scope.row)" type="text" size="mini" icon="el-icon-delete">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-if="total>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="pageSizes"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
    
    <choose-store ref="chooseStore" @confirmHandel="confirmHandel"></choose-store>
    <choose-store-and-date ref="chooseStoreAndDate" @doBatchInsertHandel="doBatchInsertHandel"></choose-store-and-date>
  </div>
</template>

<script>
  import Region from '@/components/Common/StoreCommon/Region'
  import Storage from '@/utils/localStorage'
  import ChooseStore from "@/components/common/StoreCommon/RegionChooseStore"
  import ChooseStoreAndDate from "@/components/Activity/Common/ChooseStoreAndDate"
  import * as utils from '@/utils/utils'
  import * as qiniu from 'qiniu-js'
  import config from '@/utils/config'
  const numReg = /^(0|[1-9][0-9]*)$/
  const salePriceReg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
  const fileUrlList = {
    getQiNiuToken: '/foundation/qiniu/auth/file'
  }
  const requestUrl = {
    // 获取三方平台店内分类
    getCategoryTree: '/plateform/a/category/getCategoryTree',
    // 活动商品列表
    listPage: '/plateform/a/preSale/goods/listPage',
    // 预售商品删除
    delUrl: '/plateform/a/preSale/goods/del',
    // 更新接口
    updateUrl: '/plateform/a/preSale/goods/update',
    // 发布商品到门店
    addGoodsToStore: '/plateform/a/preSale/goods/addGoodsToStore'
  }
  export default {
    data() {
      return {
        pageSizes: config.pageSizes10,
        loading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        searchContent: {
          DIFFCITY: true,
          regionCode: Storage.get('regionCode') || '', // 区域ID
          platformId: 5, // 平台类型：1-有赞；2-京东；3-美团；4-饿百；5-商城
          activitySearchVal: '',
          categoryId: '',
          categoryIdList: [],
          goodsInfo: '',
          activityStatus: '',
          activityType: '',
          platformCityGoodsId: '',
          sfInStock: ''
        },
        // 默认分类参数
        cateDefaultProps: {
          children: 'categoryVoList',
          label: 'categoryName',
          value: 'categoryId'
        },
        activityTypeOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '拼团',
            value: 11
          },
          {
            label: '秒杀',
            value: 21
          },
          {
            label: '新人专享',
            value: 41
          },
          {
            label: '每日爆款',
            value: 61
          }
        ],
        // 活动状态 0：未发布，1 ：已发布 ，2：未开始，3：进行中，4：已结束，5：已终止，6：已失效"
        activityStatusOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '进行中',
            value: 3
          },
          {
            label: '未发布',
            value: 0
          },
          {
            label: '未开始',
            value: 2
          },
          {
            label: '已结束',
            value: 4
          },
          {
            label: '已终止',
            value: 5
          }
        ],

        categoryList: [], // 商品分类id
        tableData: [], // 表格中查询结果数据
        // 多选选中的商品
        checkedList: [],

        // 是否是可编辑的更新
        showUpdate: false,
        currentIndex: -1,
        currentFiledName: '',
        showChangImg: false,
        currentRowMsg: {},
        currentChangeImgIndex: -1,
        imageUploadLoading: false,

        sfInStockOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '是',
            value: true
          },
          {
            label: '否',
            value: false
          }
        ],
        saveErrorMsgList: [],
        saveErrTempConfig: {
          skuNo: 'ERP商品编码',
          goodsName: '商品名称',
          storeNo: '门店编码',
          storeName: '门店名称',
        }
      }
    },
    created() {
      this.searchContent.activitySearchVal = this.$route.query.id && this.$route.query.id !== 'undefined' ? parseInt(this.$route.query.id) : ''
      // 三方平台店内分类
      this.getCategoryId()
    },
    mounted() {
      this.doSearchHandel(true)
    },
    components: {
      "choose-store": ChooseStore,
      "choose-store-and-date": ChooseStoreAndDate,
      "region": Region
    },
    methods: {
      RegionChange(params) {
        this.searchContent.regionCode = params.regionCode
        this.doSearchHandel(true)
      },
      /**
       * [getCategoryId 三方平台店内分类]
       * @return {[type]} [description]
       */
      getCategoryId() {
        this.$http.post(requestUrl.getCategoryTree, {DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              for(let item of res.data) {
                if(item.categoryVoList && item.categoryVoList.length === 0) {
                  item.categoryVoList = null
                }
              }
              this.categoryList = res.data
            }else {
              this.categoryList = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(三方平台店内分类):", e.message)
        })
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandels 搜索]
       * @param  {[type]} _params [description]
       * @return {[type]}         [description]
       */
      doSearchHandel(_param) {
        // 将选中数据重置为初始值
        this.checkedList = []
        let spliceLen = this.searchContent.categoryIdList.length - 1
        this.searchContent.categoryId = this.searchContent.categoryIdList[spliceLen]
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }

        this.$http.post(requestUrl.listPage, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 获取表格数据
            if(res.data) {
              if(res.data.result && res.data.result.length) {
                this.tableData = res.data.result
              }else {
                this.tableData = []
              }
              this.total = res.data.total
            }else {
              this.total = 0
              this.tableData = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })

      },
      /**
       * [deleteHandel 删除]
       * @param  {[type]} data [description]
       * @return {[type]}      [true  单个删除    false   批量删除]
       */
      deleteHandel(type, data) {
        let params = {
          DIFFCITY: true
        }
        let idAndActivityTypeArr = []
        if(type) {
          let item = {
            id: data.id,
            activityType: data.activityType
          }
          idAndActivityTypeArr.push(item)
          params.idAndActivityTypeList = idAndActivityTypeArr
        }else {
          idAndActivityTypeArr = this.checkedList.map(item => {
            return {
              id: item.id,
              activityType: item.activityType
            }
          })
          params.idAndActivityTypeList = idAndActivityTypeArr
        }
        this.$confirm('您确定要删除所选商品吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.post(requestUrl.delUrl, {...params}).then(res => {
            if(res.code === 0) {
              this.$message.success("删除成功")
              this.doSearchHandel()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [handleSelect 选择某一个]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if(utils.isKeyInArrayBykey(row.id, 'id', this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('id', row.id, this.checkedList)
          return
        }else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.id] ? '' : hash[next.id] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll 选择多个]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if(val.length > 0) {
          // 全选
          for(let item of val) {
            this.checkedList.push(item)
          }
          // 去重
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.id] ? '' : hash[next.id] = true && item.push(next)
            return item
          }, [])
        }else { 
          // 全不选 此时val.length<=0
          // 1.获取当前table中每一条的id
          let checkedListTemp = []
          for(let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.从要传递的参数中去掉这些重复的
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'id')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : [] 
        }
      },
      /**
       * [importStore 引入门店]
       * @return {[type]} [description]
       */
      importStore() {
        if(this.checkedList.length === 0) {
          this.$message("请选择商品")
          return
        }
        let activityTypeList = this.checkedList.map(item => {
          return item.activityType
        })
        let activityType = Array.from(new Set(activityTypeList))
        let activityIdList = this.checkedList.map(item => {
          return item.activityId
        })
        let activityIds = Array.from(new Set(activityIdList))
        if(activityIds.length > 1) {
          this.$notify.warning({
            title: '温馨提示',
            message: '请选择同一个活动的商品'
          })
          return
        }
        let filterActivity = this.activityTypeOptions.filter(item => {
          return item.value === activityType[0]
        })[0]
        let activityEndDate = this.checkedList.filter(item => {
          return item.activityId === activityIds[0]
        })[0].activityEndDate
        let activityName = ''
        if(filterActivity) {
          activityName = filterActivity.label
        }else {
          activityName = '-'
        }
        if(activityType[0] === 21 || activityType[0] === 61) {
          // 拼团和爆款走特殊的批量引入门店
          let param = {
            title: `批量引入门店 【${activityName}】`,
            platformId: this.searchContent.platformId,
            regionCode: this.searchContent.regionCode,
            activityType: activityType[0],
            activityEndDate: activityEndDate
          }
          this.$refs.chooseStoreAndDate.setDefault(param)
        }else {
          let param = {
            title: `批量引入门店 【${activityName}】`,
            platformId: this.searchContent.platformId,
            regionCode: this.searchContent.regionCode,
            activityType: activityType[0],
            activityEndDate: activityEndDate
          }
          this.$refs.chooseStore.setDefault(param)
        }
      },
      /**
       * [activityStatusFormatter 格式化状态]
       * @return {[type]} [description]
       */
       activityStatusFormatter(row, column, cellValue, index) {
        if(cellValue === 0) {
          return "未发布"
        }else if(cellValue == 1) {
          return "已发布"
        }else if(cellValue == 2) {
          return "未开始[发布中]"
        }else if(cellValue == 3) {
          return "进行中[发布中]"
        }else if(cellValue == 4) {
          if(row.activityType === 11) {
            return "已结束[发布中]"
          }else {
            return "已结束"
          }
        }else if(cellValue == 5) {
          return "已终止"
        }else if(cellValue == 6) {
          return "已失效"
        }
      },
      /**
       * [onFocusHandel 输入]
       * @return {[type]} [description]
       */
      onFocusHandel(index, filedName) {
        setTimeout(() => {
          this.tableData[index][`${filedName}Temp`] = this.tableData[index][filedName]
          this.showUpdate = true
          this.currentIndex = index
          this.currentFiledName = filedName
        }, 400)
      },
      onBlurHandel() {
        setTimeout(() => {
          let index = this.currentIndex
          let filedName = this.currentFiledName
          this.tableData[index][filedName] = this.tableData[index][`${filedName}Temp`]
          this.showUpdate = false
          this.currentIndex = -1
          this.currentFiledName = ''
        }, 400)
      },
      sureUpdateHandel(row) {
        let index = this.currentIndex
        let filedName = this.currentFiledName
        if(filedName === 'price') {
          // 原价
          if(!salePriceReg.test(row[filedName]) || parseFloat(row[filedName]) === 0) {
            this.$message.warning('原价是大于0，小数保留小数点后2位') 
            return
          }else {
            if(parseFloat(row[filedName]) <= parseFloat(row['preSalePrice'])) {
              this.$message.warning('原价必须大于活动价') 
              return
            }
          }
        }else if(filedName === 'preSalePrice') {
          // 活动价
          if(!salePriceReg.test(row[filedName]) || parseFloat(row[filedName]) === 0) {
            this.$message.warning('活动价是大于0，小数保留小数点后2位') 
            return
          }else {
            if(parseFloat(row[filedName]) >= parseFloat(row['price'])) {
              this.$message.warning('原价必须大于活动价') 
              return
            }
          }
        }else if(filedName === 'platformCurStock') {
          // 区域库存
          if(!numReg.test(row[filedName])) {
            this.$message.warning('区域限售库存为大于等于0的正整数') 
            return
          }
        }else if(filedName === 'limitCount') {
          // 每期限购
          if(row[filedName] && row[filedName] != 0) {
            if(!numReg.test(row[filedName])) {
              this.$message.warning('每人限购为大于等于0的正整数') 
              return
            }
          }
        }else if(filedName === 'sortNum') {
          // 排序
          if(!numReg.test(row[filedName])) {
            this.$message.warning('排序为大于等于0的正整数') 
            return
          }
        }
        // 应后台强烈要求，门店库存的值设为和区域库存一样
        row.stock = row.platformCurStock
        let rowMsg = {...row}
        let params = {
          DIFFCITY: true,
          idAndActivityTypeList: [{
            id: rowMsg.id,
            activityType: rowMsg.activityType
          }],
          updateGoodsData: rowMsg
        }
        utils.showLoading()
        this.$http.post(requestUrl.updateUrl, {...params}).then(res => {
          utils.clearLoading()
          if(res.code === 0) {
            this.$message.success("更新成功")
            this.doSearchHandel()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        }) 
      },
      /**
       * @description: 确认选择门店
       * @param {type} 
       * @return: 
       */
      confirmHandel(val) {
        let storeNos = val.checkedList.map(item => {
          return item.storeNo
        })
        let idsList = this.checkedList.map(item => {
          return item.id
        })
        let activityId = this.checkedList[0].activityId
        let activityType = val.activityType || ''
        this.$http.post(requestUrl.addGoodsToStore, {ids: idsList, storeNos: storeNos, activityType: activityType, activityId: activityId, DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            this.$refs.chooseStore.closeDialog()
            this.$refs.chooseStore.closeDisabled()
            this.$message.success("引入门店成功")
            this.doSearchHandel()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$refs.chooseStore.closeDisabled()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        }) 
      },
      mouseEnterImg(index, row) {
        if(row.activityStatus === 4 || row.activityStatus === 5 || this.imageUploadLoading === true) {
          return
        }
        this.showChangImg = true
        this.currentChangeImgIndex = index
        this.currentRowMsg = {...row}
      },
      mouseLeaveImg() {
        this.showChangImg = false
        this.currentChangeImgIndex = -1
      },
      imageUploadReg (file) {
        const isPic = file.type.indexOf('image/') !== -1
        const isJPG = file.type === 'image/jpeg' || 'image/png'
        const isSizeNum = file.size <= 1 * 300 * 1024
        if (!isPic || !isJPG || !isSizeNum) {
          this.$message.error('商品图片建议上传大小不超过300K的710*420像素的jpg或png图片')
        }
        return isPic && isJPG && isSizeNum
      },
      /**
       * [getToken 通过后台封装的接口获取七牛的token]
       * @return {[type]}            [description]
       */
       getToken () {
        return this.$http.post(fileUrlList.getQiNiuToken, {}).then(res => {
          if (res.code == 0) {
            let token = res.data.token
            let fileName = res.data.key
            return {fileName, token}
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.imageUploadLoading = false
          this.$notify.error({
            title: '获取七牛token提示',
            message: e.message
          })
        })
      },
      /**
       * [emitUpload 上传图片]
       * @param  {[type]} e [description]
       * @return {[type]}   [description]
       */
      emitUpload(e) {
        this.imageUploadLoading = true
        utils.showLoading()
        this.getToken().then((res) => {
          // 开始上传图片
          let uploader = qiniu.upload(e.file, res.fileName, res.token)
          uploader.subscribe({
            error: () => {
              this.imageUploadLoading = false
              utils.clearLoading()
            },
            complete: (res) => {
              // 添加内容设置图片地址
              let urlImage = 'http://retailimg01.benlailife.com/' + res.key
              this.imageUploadLoading = false
              utils.clearLoading()
              // 设置对应的图片地址
              this.currentRowMsg.preSaleImageUrl = urlImage
              this.sureUpdateHandel(this.currentRowMsg)
            },
            next: res => {
            }
          })
        })
      },
      /**
       * @description: 秒杀批量引入
       * @param {type} 
       * @return: 
       */
      doBatchInsertHandel(val) {
        let idsList = this.checkedList.map(item => {
          return item.id
        })
        let activityStoreGroupList = val.reqParams ? val.reqParams : []
        let activityId = this.checkedList[0].activityId
        let activityType = val.activityType || ''
        let params = {
          DIFFCITY: true,
          ids: idsList,
          activityStoreGroupList: activityStoreGroupList,
          activityId: activityId,
          activityType: activityType
        } 
        this.$http.post(requestUrl.addGoodsToStore, {...params}).then(res => {
          if(res.code === 0) {
            this.$refs.chooseStoreAndDate.closeDialog()
            this.doSearchHandel()
            if(res.data && typeof(res.data) === 'object' && res.data.length > 0) {
              this.$alert("<div><p>引入门店成功</p><p style='color: red;'>有商品在门店线上不可售</p></div>", '温馨提示', {
                dangerouslyUseHTMLString: true,
                showCancelButton: true,
                cancelButtonText: "下载明细",
                showConfirmButton: true,
                confirmButtonText: '关闭'
              }).then(() => {}).catch(() => {
                this.saveErrorMsgList = res.data
                this.downLoadSaveErrorMsg()
              });
            }else {
              this.$message.success("引入门店成功")
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$refs.chooseStoreAndDate.closeDisabled()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        }) 
      },
      downLoadSaveErrorMsg() {
        let downloadExcelConfig = {...this.saveErrTempConfig}
        const tHeader = utils.objectToArray(downloadExcelConfig, false)
        const filterVal = utils.objectToArray(downloadExcelConfig, true)
        exportSheet.ExportJsonToExcel({
          header: tHeader,
          filterVal,
          data: this.saveErrorMsgList,
          filename: `活动保存失败商品明细`
        })
        setTimeout(() => {
          this.$notify.success({
            title: '温馨提示',
            message: `活动保存失败商品明细下载成功`
          })
          this.isDisabled = false
        }, 500)
      }

    }
  }
</script>


